.Container {
  display: -ms-flexbox;
  display: flex;
  margin-right: auto;
  margin-left: auto;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 100px;
}

.subContainer1 {
  display: block;
  margin-top: 10px;
}

.subContainer2 {
  display: -ms-flexbox;
  display: flex;
}

.inputContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 5px;
  padding-bottom: 5px;
}

.inputContainer > span {
  width: 120px;
}

.labels {
  display: -ms-flexbox;
  display: flex;
  margin-top: 16px;
}

.Inputs {
  width: 100%;
  height: 19px;
}

.Container p {
  position: absolute;
  display: block;
  top: 0px;
  font-size: 18px;
  margin-bottom: 0;
  width: 300px;

  cursor: pointer;
}

.Container p:hover {
  color: #30397a;
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  transition: all 300ms;
  text-decoration: underline;
}

.btnContainer {
  display: -ms-flexbox;
  display: flex;
}

@media (min-width: 0px) and (max-width: 1079px) {
  .Container {
    position: absolute;
    margin: 0 auto 0 auto;
    left: 10%;
    right: 0;
    top: 70px;
    padding: 0 0 0px 0;
  }

  .subContainer2 {
    width: 50%;
  }

  .Container p {
    top: -50px;
  }

  .subContainer2 {
    width: 100px;
  }
}

@media (min-width: 600px) and (max-width: 1079px) {
  .Container {
    -ms-flex-pack: center;
        justify-content: center;
  }

  .inputContainer > span {
    width: 250px;
  }

  .Inputs {
    width: 220%;
    padding: 0 10px;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-right: 80px;
  }

  .subContainer1 {
    margin-right: 50px;
  }
}



