.Logo img {
    height: 50px;
    position: absolute;
    mix-blend-mode: color-dodge;
    border-radius: 50px;
}

.Logo {
    height: 100%;
    text-align: center;
    padding: 5px 0px;

}

@media (min-width:0) and (max-width:800px){
    .Logo {
        text-align: left;
        margin-left: 20px;
    }
}


